import isEqual from 'lodash/isEqual'

export default (nodes, nodePassed) => {
  let parentNodes = []
  const nodeUrl = [...nodePassed.url]
  // pop own url - check only for parents
  nodeUrl.pop()
  for (let i = 1; i < nodeUrl.length; i++) {
    parentNodes.push(nodeUrl.slice(0, i))
  }
  // remove 'Projekte' as that is not contained in openNodes
  parentNodes = parentNodes.filter(
    n => !(n.length === 1 && n[0] === 'Projekte'),
  )
  if (parentNodes.length === 0) return true

  const nodeUrls = nodes.map(n => n.url)
  console.log(`allParentNodesExist`, {
    nodes,
    nodePassed,
    parentNodes,
    nodeUrls,
  })
  const allParentNodesExist = parentNodes.every((n, i) => {
    console.log(`allParentNodesExist, ${i + 1} of ${parentNodes.length + 1}`)
    return nodeUrls.some(url => isEqual(url, n))
  })
  console.log(`allParentNodesExist, last`)
  return allParentNodesExist
}
